.TodoList-Wrap {
  padding: 20px;
  text-align: center;
  font-family: Arial, sans-serif;
  overflow-y: hidden;
}

.Title {
  margin-bottom: 20px;
  font-size: 24px;
  user-drag: none;
  user-select: none;
  -moz-user-select: none; /* Firefox 지원 */
  -webkit-user-drag: none; /* Webkit 브라우저 (Chrome, Safari) 지원 */
  -webkit-user-select: none; /* Webkit 브라우저 (Chrome, Safari) 지원 */
}

.AddTodo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  text-align: center;
}
.AddTodo .AddTitle{
  margin-top: 20px;
}

.AddTodo div {
  display: flex;
  width: 70%;
  justify-content: space-between;  
  align-items: center;
  margin-top: 20px;
}
.AddTodo input {
  padding: 5px;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
  width: 70%;
}
.AddTodo textarea {
  padding: 5px;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
  height: 300px;
  resize: none;
  width: 70%;
  margin-top: 20px;
}
.AddTodo .AddDate input {
  width: 20% !important;
}
.AddTodo button {

  margin-top: 20px;
}
.TodoList-Wrap button {
  padding: 5px 10px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}
.TodoList-Wrap button:hover {
  background: #005abb;
}

.TodoList-Wrap .TodoList {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  text-align: center;
  border-top: 1px solid #ccc;
  padding-top: 20px;
}
.TodoList-Wrap .TodoList .ListTitleWrap {
  display: flex;
  width: 100%;
  padding: 10px;
  align-content: center;
  justify-content: space-between;
  align-items: center;
}

.TodoList-Wrap .TodoList li {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 3px;
  cursor: pointer;
  transition: 0.2s;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  width: 70%;
  margin: auto;
}

.TodoList-Wrap .TodoList li:hover {
  background: #f0f0f0;
}

.TodoList-Wrap .TodoList button {
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  width: auto;
}
.TodoList-Wrap .TodoList button:hover {
  background: #aa2a37;
}
.TodoList-Wrap .TodoList .TodoDetails{
  width: 100%;
}
.TodoList-Wrap .TodoList .TodoDetails div{
  border: 1px solid#ccc;
  width: auto;  
  padding: 10px;
  margin: 5px;
}