/* 리셋 CSS */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* 텍스트 스타일 */
.MainWrap body {
  font-family: 'Arial', sans-serif;
  background-color: #f0f0f0; /* 밝은 회색 배경 */
  color: #333; /* 어두운 텍스트 색상 */
}

/* 제목 스타일 */
.MainWrap h1, h2, h3 {
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

/* 링크 스타일 */
a {
  color: #007BFF; /* 파란 링크 색상 */
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* 메인 컨테이너 */
.MainWrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  overflow: hidden;
  background: #e5f1ff;
}

/* 제목 */
.MainWrap .Title, .ItemTitle {
  font-size: 24px;
  text-align: left;
}

/* 아이템 */
.Item {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
  padding: 20px;
  width: 100%;
  text-align: left;
  display: flex;
  flex-direction: column;
}

/* 버튼 스타일 */
.defBtn {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  background-color: #007BFF;
  border: 2px solid #fff;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-left: auto;
  transition: background-color 0.3s, color 0.3s; 
}

.defBtn:hover {
  background-color: #0056b3; 
}

/* 반응형 레이아웃 */
@media (max-width: 768px) {
  .Title, .ItemTitle {
    font-size: 20px;
    width: 100%;
  }
}
