.dashboard-wrap {
  /* max-width: 1000px; */
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  background: #f2f4f6;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-evenly;
  width: 100vw;
  align-content: center;
  height: 100vh;
}

.container{
  display: flex;
  flex-direction: row;
}

.sidebar{
  color: #333;
  background: #fff;
  padding: 10px;
  width: 200px;
  margin-left: 10px;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.sidebar .userWrap{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

}
.sidebar .userWrap .mentions {
  overflow-y: auto;
  height: 1033px;  
}

.sidebar .userWrap .mentions::-webkit-scrollbar {
  width: 8px;
  background-color: transparent; 
}

.sidebar .userWrap .mentions::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 4px; 
}
.sidebar .userWrap .mentions li {

  padding: 5px 0px 0px 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 1.2;
  margin-bottom: 10px;
}

.sidebar .userPic{
background: red;
width: 100%;
width: 40px;
margin-right: 5px;
padding: 5px;
}
.sidebar .userText{
background: blue;
width: 100%;
padding: 5px;
}

h1 {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 20px;
}

.summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
}

.summary .infos {
  display: flex;
  border: solid 1px #ccc;
  border-radius: 3px;
  width: 20%;
  text-align: center;
  font-size: 1rem;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
  background: #fff;
}

.summary .infos p {
  color: #333;
  text-align: left;
  width: 100%;
}

.summary .infos p:nth-child(2) {
  font-size: 1.3rem;
  font-weight: 600;
}


.GridWrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 10px;
}

.GridWrap .GridItem {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background: #fff;
}

.GridWrap .userWrap .mentions {
  overflow-y: auto;
  height: 450px;
  width: 400px;
}

.GridWrap .userWrap .mentions::-webkit-scrollbar {
  width: 8px;
  background-color: transparent; 
}

.GridWrap .userWrap .mentions::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 4px; 
}
.GridWrap .userWrap .mentions li {

  padding: 5px 0px 0px 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 1.2;
  margin-bottom: 10px;
}

.GridWrap .userWrap .mentions .postwrap {
  padding: 5px 0px 0px 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 1.2;
  margin-bottom: 10px;
  display: flex;
}

.vertical-text {
  writing-mode: vertical-rl;
  text-orientation: mixed;
  white-space: nowrap; /* 필요에 따라 설정 */
  transform: rotate(-180deg);
  text-align: center;
}

.GridWrap .userWrap .mentions .postwrap .contents{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}


.red {
  color: red;
}

.green {
  color: rgb(0, 196, 159);
}
.GridBox3 {
  display: grid;
  justify-content: space-between;
  /* align-content: space-between; */
  grid-template-columns: 1fr 1fr;
  /* grid-template-rows: 1fr 1fr; */
  height: 100%;
  width: 100%;
  border-radius: 6px;
  background: #fff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.GridBox3 .GridBox3-Item {
  width: 100%;
  height: 100%;
  background: #fff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.GridBox3 .GridBox3-Item:nth-child(2) {
  padding-left: 0;
}
.GridBox3 .GridBox3-Item:nth-child(3) {
  grid-column: span 2;
  padding-top: 0;
}
.chart3s{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.chart3s h2{
  font-size: 1rem;
  margin: 0;
}
.tabs {
  display: flex;
  margin-bottom: 20px;
  width: 100%;
}

.tab-button {
  flex: 1 1;
  padding: 10px 0px;
  font-size: 13px;
  font-weight: 600;
  border: none;
  background-color: #fff;
  cursor: pointer;
  white-space: nowrap;
}

.tab-button.active {
  text-decoration: underline #333 2px;
  text-underline-offset: 5px;
}

.chart {
  background-color: #fff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 6px;
  width: 100%;
  height: 100%;
}
.chart2 {
  background-color: #fff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 6px;
  width: 100%;
  height: 50%;
}

.index{
  display: flex;
  justify-content: space-around;
}

/* Reach Chart Styles */
.reach-chart {
  height: 300px;
}

/* Post Share Chart Styles */
.share-chart {
  height: 300px;
}

/* New Follower Chart Styles */
.newFollowers-chart {
  height: 300px;
}

/* Likes Chart Styles */
.likes-chart {
  height: 300px;
}