.header-menu {
    position: fixed;
    top: 0;
    left: 0;
    color: #333;
    background-color: rgba(0, 0, 0, 0.1); /* 헤더 배경색을 원하는 색상으로 변경하세요 */
    z-index: 999; /* 다른 요소 위에 헤더 메뉴가 나타나도록 설정합니다. */
    transition: top 0.3s; /* 스크롤 시 부드럽게 이동하도록 설정합니다. */
    text-align: left;
    padding: 10px;
    width: auto;
}

.menu {
    font-size: 1rem;
    color: #333;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.menu a{
    color: #333;
    padding: 10px;
    width: 100%;
    padding-left: 20px;
    font-weight: 600;
}
.menu a:hover{
    background-color: rgba(0, 0, 0, 0.3);
}